.dark-mode .home p {
  color: #96a4c5;
}

.home-image {
  max-width: 700px;
  max-height: 700px;
  min-height: 350px;
  min-width: 350px;
}
.home h1 {
  font-size: 4rem;
  font-weight: 700;
  margin-top: 10rem;
}
.home p {
  font-size: 1.5rem;
  margin-top: 1rem;
  color: #818181;
}

/* media stylings */

@media (max-width: 960px) {
  .home h1 {
    font-size: 4rem;
    margin-top: 5rem;
  }
  .home p {
    font-size: 1.5rem;
  }
}

@media (max-width: 540px) {
  .home h1 {
    font-size: 2rem;
    margin-top: 1rem;
  }
  .home p {
    font-size: 1rem;
    margin-top: 0.7rem;
  }
}
