@import url('https://fonts.googleapis.com/css2?family=Mr+De+Haviland&display=swap');

.dark-mode .header {
  background-color: #22262a;
}
.dark-mode .header .header__items a {
  color: #407bff;
  background-color: #22262a;
}
.dark-mode .header .header__items a:hover {
  color: #ffffff;
}

.dark-mode .header__logo {
  color: #407bff;
}
.dark-mode .header__logo:hover {
  color: #ffffff;
}

.header {
  background-color: white;
  max-width: 100%;
  display: flex;
  justify-content: space-between;
}
.header__right {
  display: flex;
  justify-content: space-evenly;
}
.header__toggle {
  padding: 0.5rem;
}
.header__items {
  display: flex;
  list-style: none;
  font-size: 1rem;
  margin-right: 2rem;
}
.header__list {
  margin-left: 5rem;
  stroke: none;
  text-decoration: none;
  cursor: pointer;
  font-size: 1.3rem;
  font-weight: 300;
  background-color: #ffffff;
}

.header__logo {
  font-family: 'Mr De Haviland', cursive;
  margin-left: 1rem;
  font-size: 2.5rem;
  font-weight: 700;
  cursor: pointer;
  stroke: none;
  color: #2f2e41;
  text-decoration: none;
}
.header__logo:hover {
  color: #2f2e41;
}

.header .header__items a {
  cursor: pointer;
  stroke: none;
  text-decoration: none;
  margin-top: 2rem;
  color: #2f2e41;
}
.header .header__items a:hover {
  color: #407bff;
}

@media (max-width: 960px) {
  .header__items {
    display: none;
  }
}
